import React from 'react'
import { Link, NavLink } from 'react-router-dom'
import { brand_sub, community_sub, franchise_sub, mealncook_sub, menu_sub, notice_sub, store_sub } from './NavData'

const NAVLINK = [
    { title: "", link: "/brand" },
    { title: "", link: "/menu" },
    { title: "", link: "/store" },
    { title: "", link: "/franchise" },
    { title: "", link: "/notice" },
    { title: "", link: "/community" },
    { title: "", link: "/mealncook" }
]

const NavMenu = () => {
    return (
        <ul>
            <li>
                <Link to="/brand"></Link>
                <ul>
                    {
                        brand_sub.map((it, idx) => {
                            return (
                                <li key={it.id}>
                                    <Link to={it.link}>
                                        {it.title}
                                    </Link>
                                </li>
                            )
                        })
                    }
                </ul>
            </li>
            <li>
                <Link to="/menu"></Link>
                <ul>
                    {
                        menu_sub.map((it, idx) => {
                            return (
                                <li key={it.id}>
                                    <Link to={it.link}>
                                        {it.title}
                                    </Link>
                                </li>
                            )
                        })
                    }
                </ul>
            </li>
            <li>
                <Link to="/store"></Link>
                <ul>
                    {
                        store_sub.map((it, idx) => {
                            return (
                                <li key={it.id}>
                                    <Link to={it.link}>
                                        {it.title}
                                    </Link>
                                </li>
                            )
                        })
                    }
                </ul>
            </li>
            <li>
                <Link to="/franchise"></Link>
                <ul>
                    {
                        franchise_sub.map((it, idx) => {
                            return (
                                <li key={it.id}>
                                    <Link to={it.link}>
                                        {it.title}
                                    </Link>
                                </li>
                            )
                        })
                    }
                </ul>
            </li>
            <li>
                <Link to="/notice"></Link>
                <ul>
                    {
                        notice_sub.map((it, idx) => {
                            return (
                                <li key={it.id}>
                                    <Link to={it.link}>
                                        {it.title}
                                    </Link>
                                </li>
                            )
                        })
                    }
                </ul>
            </li>
            <li>
                <Link to="/community"></Link>
                <ul>
                    {
                        community_sub.map((it, idx) => {
                            return (
                                <li key={it.id}>
                                    <Link to={it.link}>
                                        {it.title}
                                    </Link>
                                </li>
                            )
                        })
                    }
                </ul>
            </li>
            <li>
                <Link to="/mealncook"></Link>
                <ul>
                    {
                        mealncook_sub.map((it, idx) => {
                            return (
                                <li key={it.id}>
                                    <Link to={it.link}>
                                        {it.title}
                                    </Link>
                                </li>
                            )
                        })
                    }
                </ul>
            </li>

            {/* {
                NAVLINK.map((it, idx) => {
                    return (
                        <li key={idx}>
                            <NavLink to={it.link}>
                                {it.title}
                            </NavLink>
                        </li>
                    )
                })
            } */}
        </ul>
    )
}

export default NavMenu