import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';

const MAINSLIDE = [
    { id: 1, title: "   NO.1", content: "     ,         ." },
    { id: 2, title: "Global Dookki", content: "    ,   110 !" },
    { id: 3, title: " - ", content: " ,     ." }
]

const MainVisual = () => {
    return (
        <section className='Main'>
            <div className="MainVisual">
                <Swiper className='MainSlider'>
                    {
                        MAINSLIDE.map((slide, idx) => {
                            return (
                                <SwiperSlide className={`itm itm0${slide.id}`} key={slide.id}>
                                    <div className="slideTxt">
                                        <img src={process.env.PUBLIC_URL + "/images/mara.png"} alt="" />
                                        <h2>{slide.title}</h2>
                                        <p>{slide.content}</p>
                                    </div>
                                </SwiperSlide>
                            )
                        })
                    }
                </Swiper>
            </div>
        </section>
    )
}

export default MainVisual