import React from 'react'
import { Link } from 'react-router-dom'
import { GoPlus } from 'react-icons/go';
import '../css/MainNews.scss'

const NEWS = [
    { id: 1, title: "     ", link: "/" },
    { id: 2, title: "    2   ", link: "/" },
    { id: 3, title: ",     ", link: "/" },
    { id: 4, title: ",      ", link: "/" },
]

const NOTICE = [
    { id: 1, title: " - :       !", link: "/" },
    { id: 2, title: " -      !", link: "/" },
    { id: 3, title: "2022   .", link: "/" },
    { id: 4, title: "2022     ", link: "/" },
]

const MainNews = () => {
    return (
        <section className="MainNews">
            <div className="inner">
                <div className="title">
                    <h2>DOOKKI NEWS</h2>
                    <p>    </p>
                </div>
                <div className="news">
                    <h3>NEWS</h3>
                    <ul>
                        {
                            NEWS.map(news => {
                                return (
                                    <li key={news.id}>
                                        <Link to="/">{news.title}</Link>
                                    </li>
                                )
                            })
                        }
                    </ul>
                    <GoPlus className='plus' />
                </div>
                <div className="notice">
                    <h3>NOTICE</h3>
                    <ul>
                        {
                            NOTICE.map(notice => {
                                return (
                                    <li key={notice.id}>
                                        <Link to="/">{notice.title}</Link>
                                    </li>
                                )
                            })
                        }
                    </ul>
                    <GoPlus className='plus' />
                </div>
            </div>
        </section>
    )
}

export default MainNews